import React, { Component } from "react";
import PropTypes from "prop-types";

export default class Modal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: this.props.show,
      result: this.props.result,
      currentFull: this.props.currentFull,
      currentThumb: this.props.currentThumb,
      images: this.props.images
    };
  }

  render() {
    const { images } = this.props;
    
    return (
    <React.Fragment>
      <div className={`modal-background ${this.props.show}`}>
      <div className="modal-container">
        <div className="modal-close-btn">
          <a href={`close-btn`} onClick={this.props.hideModal}>
            <i className="fas fa-times" />
          </a>
        </div>
        <div className={`modal ${this.props.show}`}>
            {images.map((image, index) => (
                <div key={index} className={`full ${index === 0 ? index + ' ' + this.state.currentFull: index}`}>
                    <img src={image.uri} alt="uri" />
                </div>
            ))}
        </div>
        </div>
        <div className={`modal-thumbnails ${this.props.show}`}>
        {images.map((image, index) => (
                <div key={index} className={`thumb ${index === 0 ? index + ' ' + this.state.currentThumb : index}`}>
                    <a href="#!" onClick={this.props.handleModalImg}>
                        <img src={image.uri} height={image.height} width={image.width} alt="uri" />
                    </a>
                </div>
        ))}
        </div>
        <div className="nav-btns">
            <div className="arrow-left"><i className="fas fa-chevron-left" onClick={this.props.handlePrev}></i></div>
            <div className="arrow-right"><i className="fas fa-chevron-right" onClick={this.props.handleNext}></i></div>
        </div>
      </div>
      </React.Fragment>
    );
  }
}

Modal.propTypes = {
  show: PropTypes.bool
};
