import React, { Component } from 'react';

class Footer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            footer: this.props
        }
    }
    render() {
        return (
            <footer className={`container ${this.props.footer}`}>
                <p>React Discogs Search | 2019</p>
            </footer>
        );
    }
}
export default Footer;