import React, { Component } from "react";
import Footer from '../components/Footer';
import logo from "../logo.svg";
import Modal from "./Modal";


class Artist extends Component {
  
  constructor(props) {
    super(props);
    this.state = {
      releases: [],
      result: this.props,
      show: false,
      footer: 'secondary',
      currentFull: 'current-full',
      currentThumb: 'current-thumb',
      images: []
    };
  }

  showModal = e => {
    e.preventDefault();
    this.setState({ show: true });
  };

  hideModal = e => {
    e.preventDefault();
    this.setState({ show: false });
  };

  handleClick = e => {
      e.preventDefault();
  }

  handlePrev = e => {
    e.preventDefault();
    let currentFull = document.querySelector('.current-full');
    let currentThumb = document.querySelector('.current-thumb');
    let full = document.querySelectorAll('.full');
    let thumbs = document.querySelectorAll('.thumb');
    
    currentFull.classList.remove('current-full');
    currentThumb.classList.remove('current-thumb');
    if(currentFull.previousElementSibling && currentThumb.previousElementSibling) {
      currentFull.previousElementSibling.classList.add('current-full');
      currentThumb.previousElementSibling.classList.add('current-thumb');
    } else {
      full[thumbs.length -1].classList.add('current-full');
      thumbs[thumbs.length -1].classList.add('current-thumb');
    }
  }

  handleNext = e => {
    e.preventDefault();
    let currentFull = document.querySelector('.current-full');
    let currentThumb = document.querySelector('.current-thumb');
    let full = document.querySelectorAll('.full');
    let thumbs = document.querySelectorAll('.thumb');

    currentFull.classList.remove('current-full');
    currentThumb.classList.remove('current-thumb');
    if(currentFull.nextElementSibling && currentThumb.nextElementSibling) {
      currentFull.nextElementSibling.classList.add('current-full');
      currentThumb.nextElementSibling.classList.add('current-thumb');
    } else {
      full[0].classList.add('current-full');
      thumbs[0].classList.add('current-thumb');    
    }
  }

  handleModalImg = e => {
    e.preventDefault();
    let thumbs = document.querySelectorAll('.thumb');

    for(let i = 0; i < thumbs.length; i++){
      thumbs[i].classList.remove('current');
    }
    e.target.parentNode.parentNode.classList.add('current');
  }

  componentDidMount = () => {

    this.setState({ footer: this.state.footer });

    const { id } = this.props.match.params;

    fetch(`https://api.discogs.com/artists/${id}/releases`)
      .then(res => res.json())
      .then(data => {
        this.setState({ releases: data.releases }, () =>
          console.log(this.state.releases)
        );
      });

    fetch(`https://api.discogs.com/artists/${id}?token=${process.env.REACT_APP_API_KEY}`)
      .then(res => res.json())
      .then(data => {
        console.log(data);
        this.setState(
          {
            profile: data.profile,
            images: data.images,
            realname: data.realname
          },
          () => console.log(this.state)
        );
      });
  };

  render() {
    let artistContent;
    const { id } = this.props.match.params;
    const { profile, realname, releases, images } = this.state;
    const { result } = this.props.location.state;

    if(releases === undefined || releases.length === 0) {
        return(
            <div className="loading-container">
                Loading...
            </div>
        )
    }

    artistContent = (
      <React.Fragment>
        <div className="container artist">
          <div className="artist-top">
            <div className="artist-top-left">
              <div className="artist-image">
                <a href={`/artist/${id}`} onClick={this.showModal}>
                  <img src={result.cover_image} alt={result.title} />
                </a>
              </div>
              <div className="artist-image-link">
                <a href={`/artist/${id}`} onClick={this.showModal}>
                  More Images
                </a>
              </div>
            </div>
            <div className="artist-top-center-artist-desc">
              <div className="artist-title">
                <p>{result.title}</p>
              </div>
              <div className="artist-profile">
                <p>Real Name: {realname}</p>
                <p>Profile: {profile}</p>
              </div>

              <div className="read-more">
                <a href="#!" className="readmore_toggler" onClick={this.handleClick}>
                  <span className="more">
                    More <i className="icon icon-caret-down" />
                  </span>
                  <span className="less">
                    Less <i className="icon icon-caret-up" />
                  </span>
                </a>
              </div>
            </div>
          </div>

          <div className="container table">
            <table style={{ width: "100%" }}>
              <tbody>
                <tr>
                  <th>Releases</th>
                </tr>
                {releases.filter(release => release.type==='master').map((release, index) => (
                    <tr key={index}>
                      <td key={index}>{release.title} <span className="release-year">{release.year}</span></td>                      
                    </tr>
                ))}
              </tbody>
            </table>            
          </div>
        </div>
        <Footer footer={this.state.footer} />
        <Modal
          images={images}
          currentFull={this.state.currentFull}
          currentThumb={this.state.currentThumb}
          handlePrev={this.handlePrev}
          handleNext={this.handleNext}
          handleModalImg={this.handleModalImg}        
          result={result}
          show={this.state.show}
          hideModal={this.hideModal}
        />
      </React.Fragment>
    );
    return (
      <React.Fragment>
        <div className="logo logo-search artist">
          <img src={logo} alt="logo" />
        </div>
        {artistContent}
      </React.Fragment>
    );
  }
}
export default Artist;
